import React from "react";
import classes from './Deposit.module.scss'
import {Input} from "../../components/UI/Input/Input";
import {Button} from "../../components/UI/Button/Button";
import {Preloader} from "../../components/Preloader/Preloader";
import {Title} from "../../components/Title/Title";
import {Modal} from "../../hoc/Modal/Modal";
import {FadeBg} from "../../hoc/Modal/FadeBg";
import DepoInnerWrapper from "./DepoInnerWrapper/DepoInnerWrapper";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {connect} from "react-redux";
import {
	closeModal, createDeposit, deleteDeposit,
	handleChangeDate, onChangeHandler,
	openModal,
	renderDeposit,
	selectHandler
} from "../../redux/reducers/depositReducer/actions";

// Функции для генерации полей для инпутов

class Deposit extends React.Component {
	renderInpus(inputName) {
		return Object.keys(this.props[inputName]).map((name, index) => {
			const inputObj = this.props[inputName][name];

			return (
				<Input
					key={name + index}
					type={inputObj.type}
					value={inputObj.value}
					classes={inputObj.className}
					htmlFor={name + index}
					valid={inputObj.valid}
					touched={inputObj.touched}
					label={inputObj.label}
					placeholder={inputObj.placeholder}
					errorMessage={inputObj.errorMessage}
					autocomplete={inputObj.autocomplete}
					onChange={e => this.props.onChangeHandler(e, name, inputName)}
				/>
			)
		})
	};

	componentDidMount() {
		this.props.renderDeposit();
	}

	render() {
		return (
			<div className={classes.Deposit}>
				{/* Форма ввода пополнения */}
				<div>
					<Title title="Список пополнений"/>

					<h3 className={classes.infoTitle}>
						Добавить пополнение
					</h3>

					<form onSubmit={this.props.createDeposit}>
						<div className={classes.inputsBox}>
							{
								this.renderInpus("formAddDepoInputs")
							}
							<DatePicker
								selected={this.props.depositData.date}
								// onChange={this.handleChangeDate}
								onChange={this.props.handleChangeDate}
								className={classes.datePicker}
								maxDate={new Date()}
							/>

							<select className={classes.select} onChange={this.props.selectHandler}>
								<option value="usd">USD</option>
								<option value="rub">RUB</option>
							</select>
						</div>

						<Button
							classses={["green", "addDepo"]}
							name="Добавить пополнение"
							disabled={this.props.isFormValid}
						/>
					</form>
				</div>

				{/*Общий контейнер для пополнения и статистики*/}
				<div className={classes.depoAndStatistic}>
					{/*Пополнения по годам*/}
					<h3 className={classes.infoTitle}>Общая информация по пополнениям</h3>
					{
						this.props.showPreloader
							? <Preloader/>
							: Object.keys(this.props.deposit).length === 0
							? <div className={classes.noDeposit}> У вас пока не было пополнений </div>
							: <div className={classes.DepoYear}>
								{
									Object.keys(this.props.deposit).map((year, index) => {
										const currency = this.props.deposit[year];

										return <DepoInnerWrapper
											onClick={this.props.openModal}
											currency={currency}
											year={year}
											index={index}
											key={index}
										/>
									})
								}
							</div>
					}
				</div>

				{/*	Модальное окно для подтверждения удаления */}
				{
					this.props.showModal
						? <React.Fragment>
							<Modal>
								<h2 className={classes.delModalTitle}>Вы точно хотите удалить пополнение?</h2>
								<div className={classes.modalButtons}>
									<Button
										classses={["red"]}
										name="Удалить"
										disabled={true}
										onClick={this.props.deleteDeposit}
									/>
									<Button
										classses={["green"]}
										name="Отмена"
										disabled={true}
										onClick={this.props.closeModal}
									/>
								</div>
							</Modal>
							<FadeBg onClick={this.props.closeModal}/>
						</React.Fragment>
						: null
				}
			</div>
		)
	}
}

function mapStateToProps(state) {
	// Глубокое копирование
	return JSON.parse(JSON.stringify(state.depositReducer))
}

function mapDispatchToProps(dispatch) {
	return {
		renderDeposit: () => dispatch(renderDeposit()),
		createDeposit: (e) => dispatch(createDeposit(e)),
		deleteDeposit: (e) => dispatch(deleteDeposit(e)),
		openModal: e => dispatch(openModal(e)),
		closeModal: e => dispatch(closeModal(e)),
		onChangeHandler: (e, inputName, inputObj) => dispatch(onChangeHandler(e, inputName, inputObj)),
		handleChangeDate: date => dispatch(handleChangeDate(date)),
		selectHandler: e => dispatch(selectHandler(e)),
	}
}


export default connect(mapStateToProps, mapDispatchToProps)(Deposit)