import React from "react";
import classes from "./DepoInnerWrapper.module.scss";
import DepoDateAndAmount from "../../../components/DepoDateAndAmount/DepoDateAndAmount";
import {connect} from "react-redux";

// NOTE: Оставил тут локальный стейт, так как у кажого компонента он должен быть свой. При клике это позволяет открывать каждый компонент отдельно
class DepoInnerWrapper extends React.Component {
	state = {
		isOpen: false
	};

	showMoreHandler = () => this.setState({isOpen: !this.state.isOpen});

	render() {
		return (
			<div>
				<p onClick={this.showMoreHandler} className={classes.year}>
					{this.props.year} год
				</p>

				<div className={classes.currency}>
					{this.state.isOpen
						?
						(
							Object.keys(this.props.currency).map((currName, index) => {
								// Массив конкретной валюты (Usd или rub)
								const currencyArr = this.props.currency[currName];

								return (
									<div key={index} className={classes.DepoCurrency}>
										<h4 className={classes.name}>{currName === 'rub' ? "Рубли" : currName === "usd" ? "Доллары" : null}</h4>

										<ul>
											<DepoDateAndAmount
												onClick={this.props.onClick}
												currency={currName}
												depo={currencyArr}/>
										</ul>
									</div>
								)
							})
						)
						: null}
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	// Глубокое копирование
	return JSON.parse(JSON.stringify(state.depositReducer))
}

function mapDispatchToProps(dispatch) {
	return {
		// showMoreDepoForYear: () => dispatch(showMoreDepoForYear()),
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(DepoInnerWrapper)