import {
	CHANGE_SELECT,
	DEPOSIT_CLOSE_MODAL,
	ON_CHANGE,
	OPEN_MODAL,
	RENDER
} from "../../actions/actionsType";

const initState = {
	user: null,
	isFormValid: false,
	showModal: false,
	showPreloader: true,
	deposit: {},
	formAddDepoInputs: {
		amount: {
			type: "text",
			errorMessage: "Введите число",
			className: ["depositInput"],
			placeholder: "Введите сумму",
			autocomplete: "off",
			valid: false,
			touched: false,
			value: "",
			validation: {
				required: true,
				type: "number"
			}
		},
	},
	depositData: {
		currency: "usd",
		amount: "",
		date: Date.parse(new Date)
	},
	data: {},
	isOpen: false
};

export default function depositReducer(state = initState, action) {
	switch (action.type) {

		case RENDER:
			return {
				...state,
				...action
			};
		case OPEN_MODAL:
			return {
				...state,
				...action
			};
		case DEPOSIT_CLOSE_MODAL:
			return {
				...state,
				showModal: false
			};
		case ON_CHANGE:
			return {
				...state,
				...action
			};
		case CHANGE_SELECT:
			return {
				...state,
				...action
			};

		default:
			return state
	}
}