import {AUTH_AUTO_LOGIN, AUTH_INPUT, AUTH_LOGIN, AUTH_LOGOUT} from "../../actions/actionsType";

const initState = {
	token: null,
	isFormValid: false,
	authInputs: {
		inputEmail: {
			type: "email",
			label: "Введите e-mail",
			errorMessage: "Введите e-mail",
			className: ["authLogin"],
			placeholder: "Введите e-mail",
			autocomplete: "off",
			valid: false,
			touched: false,
			value: "",
			validation: {
				required: true,
				type: "email"
			}
		},
		inputPassword: {
			type: "password",
			label: "Введите пароль",
			errorMessage: "Пароль не может быть менее 6 символов",
			className: ["authLogin"],
			placeholder: "Введите пароль",
			autocomplete: "off",
			valid: false,
			touched: false,
			value: "",
			validation: {
				required: true,
				minLength: 6
			}
		}
	},

};

export default function authReducer(state = initState, action) {
	switch (action.type) {
		case AUTH_INPUT:
			return {
				...state,
				...action
			};
		case AUTH_LOGIN:
			return {
				...state,
				token: action.token,
				authInputs: action.authInputs
			};
		case AUTH_AUTO_LOGIN:
			return {
				...state,
				token: action.token
			};
		case AUTH_LOGOUT:
			return {
				...state,
				token: null
			};
		default:
			return state
	}
}