import React from "react";
import classes from "./Auth.module.scss"
import {connect} from "react-redux";
import {Input} from "../../components/UI/Input/Input";
import {
	authChangeHandler,
	authForm,
	loginHandler
} from "../../redux/reducers/authReducer/actions";
import {Title} from "../../components/Title/Title";
import {Button} from "../../components/UI/Button/Button";

class Auth extends React.Component {
	renderInput(inputObj) {

		return Object.keys(this.props[inputObj]).map((inputName, index) => {
			const input = this.props[inputObj][inputName];

			return (
				<Input
					key={index}
					type={input.type}
					label={input.label}
					errorMessage={input.errorMessage}
					className={input.className}
					placeholder={input.placeholder}
					valid={input.valid}
					touched={input.touched}
					value={input.value}
					validation={input.validation}
					onChange={e => this.props.authChangeHandler(e, inputObj, inputName)}
				/>)
		});
	}

	render() {
		return (
			<div className={classes.Auth}>
				<Title title={"Добро пожаловать!"}/>
				<p className={classes.greetings}>Пожалуйста, войдите в систему или зарегистрируйтесь</p>

				<div className={classes.authForm}>
					<form onSubmit={this.props.authForm}>
						{this.renderInput("authInputs")}

						<div className={classes.btnContainer}>
							<Button
								classses={["green", "authEntry"]}
								name="Войти"
								disabled={this.props.isFormValid}
								onClick={this.props.loginHandler.bind(this, true)}
							/>
							<Button
								classses={["yellow", "authReg"]}
								name="Регистрация"
								disabled={this.props.isFormValid}
								onClick={this.props.loginHandler.bind(this, false)}
							/>
						</div>
					</form>
				</div>
			</div>
		)
	}
}

function mapStateToProps(state) {
	// Глубокое копирование
	return JSON.parse(JSON.stringify(state.authReducer))
}

function mapDispatchToProps(dispatch) {
	return {
		authChangeHandler: (e, inputObj, inputName) => dispatch(authChangeHandler(e, inputObj, inputName)),
		authForm: (e) => dispatch(authForm(e)),
		loginHandler: (isLogin) => dispatch(loginHandler(isLogin))
	}
}


export default connect(mapStateToProps, mapDispatchToProps)(Auth)