import React, {Component} from "react";
import "./App.scss";
import Layout from "./hoc/Layout/Layout";
import Main from "./container/Main/Main";
import Deposit from "./container/Deposit/Deposit";
import SidebarNavigation from "./components/SidebarNavigation/SidebarNavigation";
import Header from "./components/Header/Header";
import {Route, Switch, Redirect} from "react-router-dom"
import {connect} from "react-redux";
import Auth from "./container/Auth/Auth";
import {autoLogin} from "./redux/reducers/authReducer/actions";

class App extends Component {

	componentDidMount() {
		this.props.autoLogin();
	}

	render() {
		return (
			<Layout>
				<Header/>
				{
					this.props.token
						?
						<div className="main-page-wrapper">
							<SidebarNavigation/>

							<div className={'main-page-content'}>
								<Switch>
									<Route exact={true} path={"/"} component={Main}/>
									<Route path={"/depo"} component={Deposit}/>
									<Redirect to={"/"} component={Main}/>
								</Switch>
							</div>
						</div>
						:
						<div className="main-page-wrapper">
							<Route>
								<Route exact path={"/auth"} component={Auth}/>
								<Redirect to={"/auth"} component={Auth}/>
							</Route>
						</div>
				}
			</Layout>
		);
	}
}


function mapStateToProps(state) {
	// Глубокое копирование
	return JSON.parse(JSON.stringify(state.authReducer))
}

function mapDispatchToProps(dispatch) {
	return {
		autoLogin: () => dispatch(autoLogin()),
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
