import React from "react";
import classes from './SidebarNavigation.module.scss'
import LinksInsideSidebar from "../LinksInsideSidebar/LinksInsideSidebar";

function SidebarNavigation() {
	return (
		<div className={classes.SidebarNavigation}>
			<LinksInsideSidebar/>
		</div>
	)
}

export default SidebarNavigation