import {createAddFormInputs, createUpdFormInputs} from "../../../Modules/formValidation";
import {
	CHANGE_SELECT,
	CLOSE_MODAL, CLOSE_PRELOADER,
	CREATE_COMPANY,
	CURRENCY,
	GET_COMPANY_DATA,
	OPEN_MODAL_UPDATE,
	RENDER, SHOW_PRELOADER
} from "../../actions/actionsType";

// Функции создания инпутов
function createOptionForAddInputs(name, errMessage, obj) {
	return createAddFormInputs({
			label: name,
			type: "text",
			errorMessage: errMessage,
			className: [""],
			autocomplete: "off"
		}, obj
	)
}

function createOptionForUpdInputs(name, errMessage, obj) {
	return createUpdFormInputs({
			label: name,
			type: "text",
			errorMessage: errMessage,
			className: [""],
			autocomplete: "off"
		}, obj
	)
}

const initState = {
	user: null,
	nameForDelete: "",
	showPreloader: true,
	isFormValid: false,
	modals: {
		isOpenModalAddCompany: false,
		isOpenModalUpdateCompany: false
	},
	formAddInputs: {
		name: createOptionForAddInputs("Имя компании", "Поле не может быть пустым", {required: true}),
		count: createOptionForAddInputs("Количество акций", "Введите число", {required: true, type: "number", }),
		dividendPerShare: createOptionForAddInputs("Размер дивидендов (в год)", "Введите число", {
			required: true,
			type: "number"
		})
	},
	formUpdInputs: {
		count: createOptionForUpdInputs("Количество акций", "Введите число", {required: true, type: "number"}),
		dividendPerShare: createOptionForUpdInputs("Размер дивидендов (в год)", "Введите число", {
			required: true,
			type: "number"
		})
	},
	company: {
		name: "",
		count: "",
		dividendPerShare: "",
		currency: "USD"
	},
	companies: {},
	currency: [],
};

//Всегда возвращаем новый объект, сохраняем иммутабельность
// В редюсерах по сути мы обновляем стейт. В action передаем то, что нужно обновить

export default function mainReducer(state = initState, action) {
	switch (action.type) {
		case RENDER: {
			return {
				...state,
				...action
			}
		}

		case CURRENCY:
			return {
				...state,
				currency: action.currency
			};

		case CREATE_COMPANY:
			return {
				...state,
				...action
			};

		case SHOW_PRELOADER:
			return {
				...state,
				showPreloader: true

			};
		case CLOSE_PRELOADER:
			return {
				...state,
				showPreloader: false
			};

		case GET_COMPANY_DATA:
			return {...state, ...action};

		case OPEN_MODAL_UPDATE:
			return {
				...state,
				modals: action.modals,
				nameForDelete: action.nameForDelete
			};

		case CLOSE_MODAL:
			return {
				...state,
				modals: action.modals,
				formAddInputs: action.formAddInputs,
				isFormValid: action.isFormValid
			};

		case CHANGE_SELECT:
			return {
				...state,
				company: action.company
			};

		default:
			return state
	}
}

// Функция открытия модального окна
