import React from "react";
import classes from './Main.module.scss'
import {Title} from "../../components/Title/Title";
import {Button} from "../../components/UI/Button/Button";
import {Modal} from "../../hoc/Modal/Modal";
import CompaniesList from "../../components/CompaniesList/CompaniesList";
import {Input} from "../../components/UI/Input/Input";
import {FadeBg} from "../../hoc/Modal/FadeBg";
import {Preloader} from "../../components/Preloader/Preloader";
import {connect} from "react-redux";
import {
	clickOnCompany,
	closeModal,
	createCompany,
	deleteCompany,
	getCompanyDataChangeHandler, noCompanies,
	openModal,
	renderCompanies,
	selectHandler, updateCompany
} from "../../redux/reducers/mainReducer/actions";

class Main extends React.Component {
	renderInputs(name) {
		return Object.keys(this.props[name]).map((inputName, index) => {
			const inputObj = this.props[name][inputName];

			return (
				<Input
					key={inputName + index}
					type={inputObj.type}
					value={inputObj.value}
					classes={inputObj.className}
					htmlFor={inputName + index}
					valid={inputObj.valid}
					touched={inputObj.touched}
					label={inputObj.label}
					errorMessage={inputObj.errorMessage}
					autocomplete={inputObj.autocomplete}
					onChange={e => this.props.getCompanyData(e, inputName, name)}
				/>
			)
		})
	};

	componentDidMount() {
		this.props.renderCompanies();
	}

	render() {
		return (
			<div className={classes.Main}>
				<Title title="Список компаний"/>
				<h2 className={classes.infoTitle}>Дивиденды в портфеле</h2>
				{
					this.props.showPreloader
						? <Preloader/>
						: Object.keys(this.props.companies).length === 0
						? <div className={classes.noCompany}>Вы еще не создали ни одной компании</div>
						: this.props.currency.map((currency, index) => {
							return (
								<div className={classes.tableWrapper} key={index}>
									<h3>{currency}</h3>
									<CompaniesList
										currency={currency}
										companies={this.props.companies}
										showModalHandlerDelete={e => this.props.clickOnCompany(e)}
									/>
								</div>
							)
						})
				}
				<div className={classes.addSection}>
					<Button
						classses={["green"]}
						name="Добавить компанию"
						onClick={e => this.props.openModal(e, "isOpenModalAddCompany")}
						disabled={true}
					/>
				</div>
				{/* Модальное окно для добавления компании */}
				{
					this.props.modals.isOpenModalAddCompany
						?
						<React.Fragment>
							<Modal>
								<h3 className={classes.MainModalTitle}>Добавить компанию</h3>

								<form onSubmit={this.props.createCompany}>
									{
										this.renderInputs("formAddInputs")
									}
									<select onChange={this.props.selectHandler}>
										<option value="USD">USD</option>
										<option value="RUB">RUB</option>
									</select>
									<Button
										disabled={this.props.isFormValid}
										classses={["blue"]}
										name="Создать компанию"
									/>
								</form>
							</Modal>
							<FadeBg onClick={e => this.props.closeModal(e, "isOpenModalAddCompany")}/>
						</React.Fragment>
						: null
				}
				{/* Модальное окно для удаления/редактирования компании */}
				{
					this.props.modals.isOpenModalUpdateCompany
						?
						<React.Fragment>
							<Modal>
								<h3 className={classes.deleteModal}>
									Карточка компании "{this.props.nameForDelete}"
								</h3>
								{
									this.renderInputs("formUpdInputs")
								}
								<div className={classes.modalDeleteBtnBox}>

									<Button
										name="Удалить"
										classses={["red"]}
										onClick={e => this.props.deleteCompany(this.props.nameForDelete)}
										disabled={this.props.isFormValid}
									/>

									<Button
										name="Изменить"
										classses={["green"]}
										onClick={this.props.updateCompany}
										disabled={this.props.isFormValid}
									/>
								</div>
							</Modal>
							<FadeBg onClick={e => this.props.closeModal(e, "isOpenModalUpdateCompany")}/>
						</React.Fragment>
						: null
				}
			</div>
		)
	}
}

function mapStateToProps(state) {
	// Глубокое копирование
	return JSON.parse(JSON.stringify(state.mainReducer))
}

function mapDispatchToProps(dispatch) {
	return {
		renderCompanies: () => dispatch(renderCompanies()),
		createCompany: e => dispatch(createCompany(e)),
		updateCompany: () => dispatch(updateCompany()),
		deleteCompany: name => dispatch(deleteCompany(name)),
		clickOnCompany: e => dispatch(clickOnCompany(e)),
		getCompanyData: (e, inputName, inputsObj) => dispatch(getCompanyDataChangeHandler(e, inputName, inputsObj)),
		openModal: (e, modalName) => dispatch(openModal(e, modalName)),
		closeModal: (e, modalName) => dispatch(closeModal(e, modalName)),
		selectHandler: e => dispatch(selectHandler(e)),
		// noCompanies: () => dispatch(noCompanies()),
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);